// src/components/Director.js
import React, { useEffect } from "react";
import "./Director.css";
import directorImage from "../assets/herodirector.jpg"; // Update the path if necessary
import awardImage from "../assets/award.png"; // Update the path if necessary
import directorImageosama from "../assets/osama.png";

// Import film posters
// import alaAlhawaPoster from "../assets/posters/Poster ALsraya Part 1.png";
// import nihomSayedAlKalimatPoster from '../assets/posters/Nihom_Sayed_Al-Kalimat.jpg';
// import everyDayAStoryPoster from '../assets/posters/Every_Day_a_Story.jpg';
// import phobiaPoster from '../assets/posters/Phobia.jpg';
import theRandomPoster from "../assets/posters/The Random.jpeg";
import dragunovPoster from "../assets/posters/Poster dragnof.jpg";
// import ayadPoster from '../assets/posters/Ayad.jpg';
import rubikPoster from "../assets/posters/Rubik.png";
import zankaAlReehPoster from "../assets/posters/zangat alreh.jpg";
import azzaimanPoster from "../assets/posters/Poster Zaeman.jpg";
import ghasakPoster from "../assets/posters/Poster Ghasaq.jpg";
import zankatArreehPartTwoPoster from "../assets/posters/zangat alreh.jpg";
// import albaroniPoster from '../assets/posters/Albaroni.jpg';
import alsarayaPoster from "../assets/posters/el saraya final option 3 V1.jpg";
import alsarayaPartTwoPoster from "../assets/posters/Poster ALsraya Part 1.png";
import banatAlamPoster from "../assets/posters/bnat.jpg";

const Director = () => {
   const filmography = [
      //   { title: "Ala Alhawa", year: 2008, poster: alaAlhawaPoster },
      // { title: "Nihom Sayed Al-Kalimat", year: 2008, poster: nihomSayedAlKalimatPoster },
      // { title: "Every Day a Story", year: 2010, poster: everyDayAStoryPoster },
      // { title: "Phobia", year: 2012, poster: phobiaPoster },
      { title: "The Random", year: 2013, poster: theRandomPoster },
      { title: "Dragunov", year: 2014, poster: dragunovPoster },
      // { title: "Ayad", year: 2015, poster: ayadPoster },
      { title: "Rubik", year: 2017, poster: rubikPoster },
      { title: "Zanka Al-Reeh", year: 2019, poster: zankaAlReehPoster },
      { title: "Azza’iman", year: 2020, poster: azzaimanPoster },
      { title: "Ghasak", year: 2021, poster: ghasakPoster },
      { title: "Zankat Arreeh - Part Two", year: 2021, poster: zankatArreehPartTwoPoster },
      // { title: "Albaroni", year: 2021, poster: albaroniPoster },
      { title: "Alsaraya", year: 2022, poster: alsarayaPoster },
      { title: "Alsaraya - Part Two", year: 2023, poster: alsarayaPartTwoPoster },
      { title: "Banat Alam", year: 2024, poster: banatAlamPoster },
   ];

   const awards = [
      {
         title: "Second best drama series for Zankat Arreeh",
         event: "ASBU Festival in Tunisia 2021",
      },
      { title: "Best drama series for Asaraya", event: "ASBU Festival in Jeddah 2022" },
      { title: "Best documentary film director award", event: "2009" },
      { title: "Best program director award", event: "Youth magazine 2010" },
      { title: "Best program director award", event: "Youth magazine 2013" },
      { title: "Best drama director award", event: "Septimius Award 2014" },
      { title: "Best program director award", event: "Alamat Estifham 2013" },
      { title: "Best drama director award for Phobia", event: "Septimius Award 2014" },
      { title: "Best drama director award for Dragunov", event: "Septimius Award 2015" },
      { title: "Best drama director award for Zankat Arreeh", event: "Septimius Award 2019" },
      { title: "Best drama director award for The Two Leaders", event: "Septimius Award 2020" },
      { title: "Best drama director award for Ghasaq", event: "Septimius Award 2021" },
      { title: "Best drama director award for Alsaraya – Part One", event: "Septimius Award 2023" },
      { title: "Bronze award for The Random", event: "Arab Film Festival in Gabes, Tunisia 2016" },
      {
         title: "Special mention for The Random",
         event: "Maghrebi Film Festival in Oujda, Morocco, 2016",
      },
      { title: "Best African film award for Random", event: "ART City Festival in Cameroon 2016" },
   ];

   useEffect(() => {
      const sections = document.querySelectorAll('.director-section');

      const observer = new IntersectionObserver(entries => {
         entries.forEach(entry => {
            if (entry.isIntersecting) {
               entry.target.classList.add('fade-in');
               observer.unobserve(entry.target);
            }
         });
      }, {
         threshold: 0.1,
      });

      sections.forEach(section => {
         observer.observe(section);
      });

      return () => {
         if (sections.length) {
            sections.forEach(section => observer.unobserve(section));
         }
      };
   }, []);

   return (
      <div className="director-container">
         <div className="director-hero">
            <img src={directorImage} alt="Director" className="director-image" />
            <div className="director-info">
               <h1 className="director-title">Osama Rezg</h1>
               <p className="director-subtitle">A Journey Through Cinema</p>
            </div>
         </div>
         <div className="director-content">
            <section className="director-section biography">
               <h2>Biography</h2>
               <div className="bio-card">
                  <div className="bio-text">
                     <h3>
                     Osama Rezg, born on February 13, 1981, in Tripoli, Libya, is a notable Libyan director and producer. He holds a master’s degree in cinema science and drama from Bilgi University in Turkey and has several certifications in film directing and production from prestigious institutions in London and Scotland. Rezg's career spans various roles in the media industry, including directing commercials, documentaries, dramas, and films. His work has earned him over 16 awards from international, Arab, and local festivals, making him a prominent figure in Libyan arts. He is dedicated to advancing Libyan drama with high-quality, contemporary productions.
                     </h3>
                  </div>
                  <img src={directorImageosama } alt="Osama Rezg" className="bio-image" />
               </div>
            </section>
            <section className="director-section filmography">
               <h2>Filmography</h2>
               <div className="grid-container">
                  {filmography.map((item, index) => (
                     <div key={index} className="grid-item">
                        <div className="poster">
                           <img src={item.poster} alt={item.title} />
                        </div>
                        <div className="poster-info">
                           <h3>{item.title}</h3>
                           <p>{item.year}</p>
                        </div>
                     </div>
                  ))}
               </div>
            </section>
            <section className="director-section awards">
               <h2>Awards</h2>
               <div className="grid-container">
                  {awards.map((item, index) => (
                     <div key={index} className="grid-item">
                        <div className="award">
                           <img src={awardImage} alt="Award" />
                        </div>
                        <div className="award-info">
                           <h3>{item.title}</h3>
                           <p>{item.event}</p>
                        </div>
                     </div>
                  ))}
               </div>
            </section>
         </div>
      </div>
   );
};

export default Director;
